}

.header {
  background: var(--primary);
}

.header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(/public/lines.gif);
  background-size: cover;
  background-position: fixed;
  opacity: 0.01;
}
